.resume-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: fit-content;
  margin: 120px 0;
}

.resume-card {
  display: flex;
  height: 400px;
  width: 1000px;
}
.resume-bullets {
  height: 100%;
  box-shadow: 15px 0 9px -15px #1f2235;
  width: 320px;
}

.resume-bullet-details {
  flex-grow: 1;
  width: 600px;
  max-height: 400px;
  padding: 0 0 0 60px;
  overflow-y: auto;
}

.experience-description {
  margin: 10px 0 0 0;
  text-align: justify;
  max-width: 100%;
}

.experience-description-text {
  font-size: 12px;
}

.resume-details-carousal {
  transition: transform 1s ease-out;
  overflow-y: auto;
}

.bullet-container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;
}

.bullets {
  width: 80%;
  position: relative;
  z-index: 2;
}

.bullet {
  display: flex;
  align-items: center;
  background-color: #1f2235;
  height: 40px;
  margin: 15px 0;
  padding: 0 8px;
  border-radius: 20px;
  width: 30px;
  transition: width 0.6s ease;
  cursor: pointer;
}

.bullet-label {
  font-size: 14px;
  white-space: nowrap;
  font-family: "Poppins SemiBold";
}

.bullet-logo {
  height: 16px;
  margin: 0 30px 0 0;
}

.bullet-icons {
  width: 34px;
  height: 100%;
  z-index: 1;
  background-color: #1f2235;
  position: absolute;
}

.selected-bullet {
  color: white;
  width: 100%;
}

.resume-screen-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  min-height: 400px;
  height: 100%;
  width: 100%;
}

.resume-heading {
  display: flex;
  flex-direction: column;
}
.resume-main-heading {
  position: relative;
  display: flex;
  justify-content: space-between;
}
.resume-main-heading span {
  color: var(--dark-orange);
  font-size: 16px;
  font-family: "Poppins SemiBold";
}

.heading-bullet {
  position: absolute;
  left: -30px;
  height: 15px;
  width: 15px;
  top: 5px;
  background-color: #ff5823;
  border-radius: 50%;
}

.resume-sub-heading {
  font-size: 14px;
  font-family: "Poppin SemiBold";
  color: black;
  font-weight: 550;
  margin: 10px 0 0 0;
}

.resume-heading-description {
  font-size: 12px;
  text-align: justify;
}

.heading-date {
  background-color: #ff5823;
  padding: 4px 12px;
  font-size: 14px;
  border-radius: 14px;
  color: white;
}

.skill-parent {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 50%;
}
.programming-skills-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.skill-percentage {
  position: relative;
  height: 15px;
  width: 70%;
  background-color: #ff5823;
}

.skill-percentage .active-percentage-bar {
  height: 15px;
  transition: 0.8s ease;
  position: absolute;
  background-color: #1f2235;
}

.skill-parent span {
  font-family: "Poppins SemiBold";
  font-size: 16px;
}
@media only screen and (max-width: 1110px) {
  .resume-card {
    width: 880px;
  }
  .resume-bullet-details {
    padding: 0 0 0 70px;
  }
}

@media only screen and (max-width: 1023px) {
  .resume-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .bullets {
    width: 90%;
  }

  .resume-card {
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin: 30px 0;
    align-items: center;
    width: 90%;
  }

  .resume-bullet-details {
    height: 100%;
    padding: 0;
    width: fit-content;
  }

  .resume-bullets {
    margin: 30px 0;
    width: 100%;
  }

  .selected-bullet {
    width: 100%;
  }
}
@media only screen and (max-width: 414px) {
  .heading-date {
    padding: 1vw 1.6vw !important;
    font-size: 3.5vw !important;
    white-space: nowrap !important;
    max-height: 6.5vw !important;
  }
  .resume-main-heading span {
    font-size: 4.2vw !important;
  }
  .experience-description {
    font-size: 3.5vw !important;
  }
  .resume-sub-heading {
    font-size: 3.8vw !important;
  }
  .heading-bullet {
    display: none;
  }
  .resume-heading-description {
    font-size: 3.5vw !important;
  }
  .skill-parent span {
    font-size: 4.2vw !important;
  }
}
@media only screen and (max-width: 520px) {
  .heading-date {
    padding: 4.12px 6.5px;
    font-size: 14.4px;
    white-space: nowrap;
    max-height: 26.7px;
  }
  .resume-main-heading span {
    font-size: 19px;
  }
  .experience-description {
    font-size: 14.4px;
  }
  .resume-sub-heading {
    font-size: 15.65px;
  }
  .heading-bullet {
    display: none;
  }
  .resume-heading-description {
    font-size:14.4px;
  }
  .skill-parent span {
    font-size: 17.3px;
  }
}

@media only screen and (max-width: 768px) {
  .heading-bullet {
    display: none;
  }
}
